<script>
import categoriasProductos from "../assets/js/categoriasProductos.json";
import buscados from '../assets/js/mas_buscados.json';
import CustomPath from "@/components/CustomPath.vue";
import { slavonMixin } from "@/assets/js/slavonMixin.js";
import MaskRedirectButtonVue from "../components/Common/MaskRedirectButton.vue";
import footerMenu from "../components/Servicios/footerMenu.vue";
import BannerWithBot from "../components/Common/BannerWithBot.vue";
import seccionEco from "../components/Servicios/seccionEco.vue";
import PicZoomWithText from "../components/Common/PicZoomWithText.vue";

export default {
  mixins: [slavonMixin],
  components: { 
    CustomPath,
    MaskRedirectButtonVue,
    'footer-menu': footerMenu,
    'banner-box': BannerWithBot,
    'eco-seal': seccionEco,
    'mas-buscados': PicZoomWithText,
  },
  data: function () {
    return {
      lastScrollPosition: 0,
      showNavbar: false,
      categorias: categoriasProductos.categorias,
      masBuscados: buscados.buscados,
      basePath: [
        {
          sectionName: "Inicio",
          sectionUrl: "/",
        },
        {
          sectionName: "Productos",
          sectionUrl: "/productos",
        },
      ],
      bannerTodosLosProductos: require(`@/assets/images/productos/Banner Equipamiento.jpg`),
    };
  },
  methods: {
    onScroll() {
      let width = window.innerWidth;
      if (width > 650) {
        var currentScrollPosition =
          window.pageYOffset || document.documentElement.scrollTop;
        if (currentScrollPosition < 0) {
          return;
        }

        this.showNavbar = currentScrollPosition > 300;
        this.lastScrollPosition = currentScrollPosition;
      }
    },
    generameElPathCompleto(nombre, url) {
      let slavon = this.dameUnSlavon(nombre, url);
      let auxPath = this.basePath;
      auxPath.push(slavon);
      return auxPath;
    },
  },
  mounted() {
    window.addEventListener("scroll", this.onScroll);
  },
  destroyed() {
    window.removeEventListener("scroll", this.onScroll);
  },
  computed: {
    diagnosticoImagenesPath() {
      this.generameElPathCompleto("Diagnóstico por Imágenes", "/productos");
    },
    veterinariaPath() {
      this.generameElPathCompleto("Division Veterinaria", "/productos");
    },
  },
};
// {
